$buttonColor: #2b2b2b;

* {
  box-sizing: border-box;
  transition: 0.333s ease;
}

.rela-block {
  display: block;
  position: relative;
  margin: auto;
}

.rela-inline {
  display: inline-block;
  position: relative;
  margin: auto;
}

.floated {
  display: inline-block;
  position: relative;
  float: left;
}

.abs-center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  width: 88%;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 18px;
  letter-spacing: 2px;
  font-weight: 400;
  line-height: 24px;
  background-color: #ececec;
}

.photo-container {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  opacity: 0;
  background-color: rgba(0, 0, 0, 0.73);

  &.displayed {
    z-index: 200;
    opacity: 1;
  }

  .photo-tab {
    top: 45%;
  }

  .photo-details {
    bottom: 4%;
    transition: 0.35s ease, bottom 0.35s 0.35s ease;
  }

  .close {
    color: white;
    margin: 30px;
    cursor: pointer;
  }
}

.photo-details {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  width: 60%;
  max-width: 700px;
  font-family: "Open Sans", sans-serif;
  font-size: 14px;
  line-height: 24px;

  h2 {
    font-family: "Montserrat", sans-serif;
    font-size: 18px;
    text-transform: uppercase;
  }

  ul {
    text-align: justify;
    margin-left: -15px;
  }

  li {
    margin: auto 15px;
  }

  span {
    font-weight: 600;
  }
}

.photo-tab {
  z-index: 300;
  top: 55%;
  width: 50%;
  padding-bottom: 50%;
  margin: auto;
  background-size: contain;
  background-position: center;
}

.side-menu {
  position: fixed;
  top: 0;
  left: -360px;
  width: 360px;
  z-index: 100;
  background-color: #1a1a1a;
  padding-top: 80px;

  .list-header {
    color: #888;
    margin-bottom: 10px;
    padding-left: 30px;
  }

  .options-list {
    margin-bottom: 40px;

    li {
      height: 50px;
      color: #ccc;
      line-height: 50px;
      padding-left: 30px;
      transition: 0.15s ease;
      cursor: pointer;

      &:hover {
        background-color: rgba(255, 255, 255, 0.2);
        color: #fff;
      }
    }
  }

  .social-buttons-container {
    position: absolute;
    top: 0;
    left: 0;
    height: 60px;
    text-align: center;
  }

  .social-button {
    height: 55px;
    width: 55px;
    margin: 0 5px;
    background-size: cover;
    transform: scale(0.75);
    cursor: pointer;

    &.twitter {
      background: url("https://cdn2.iconfinder.com/data/icons/black-white-social-media/32/twitter_online_social_media-128.png")
        no-repeat center;
    }

    &.facebook {
      background: url("https://cdn2.iconfinder.com/data/icons/black-white-social-media/32/online_social_media_facebook-128.png")
        no-repeat center;
    }

    &.pinterest {
      background: url("https://cdn2.iconfinder.com/data/icons/black-white-social-media/32/online_social_media_pinterest-128.png")
        no-repeat center;
    }

    &.instagram {
      background: url("https://cdn2.iconfinder.com/data/icons/black-white-social-media/32/instagram_online_social_media_photo-128.png")
        no-repeat center;
    }

    &:hover {
      transform: scale(1);
      transition: 0.3s cubic-bezier(0.3, 0.2, 0, 2.5);
    }
  }
}

.top-bar {
  z-index: 5;
  height: 200px;
  background-color: #1a1a1a;
  text-align: center;
  overflow: hidden;

  .link {
    position: relative;
    font-size: 14px;
    top: 30px;
    text-decoration: none;
    color: #999;
  }
}

.checkbox-label {
  position: absolute;
  top: 20px;
  left: 20px;
  z-index: 101;
  height: 40px;
  width: 40px;
  cursor: pointer;

  &:after {
    height: 100%;
    font-family: "Open Sans", sans-serif;
    font-size: 18px;
    color: black;
    opacity: 0;
    transition: 0.25s ease;
  }

  &:hover:after {
    opacity: 1;
  }

  &.side-menu-label {
    opacity: 1;
  }

  &.menu-label {
    &:after {
      content: "MENU";
      position: absolute;
      top: 45%;
      left: 20%;
    }

    &:hover:after {
      left: 100%;
    }
  }

  &.search-label {
    &:after {
      content: "SEARCH";
      position: absolute;
      top: -100%;
      left: 20%;
    }

    &:hover:after {
      left: -170%;
    }
  }
}

.black-lines {
  height: 2px;
  width: 18px;
  background-color: #222;

  &:before,
  &:after {
    content: "";
    position: absolute;
    left: 0;
    height: 2px;
    width: 26px;
    background-color: #222;
    transition: 0.2s ease;
  }

  &:before {
    top: -7px;
  }

  &:after {
    top: 7px;
  }
}

.magnifying-glass {
  height: 32px;
  width: 36px;

  &:before {
    content: "";
    position: absolute;
    top: 3px;
    left: 3px;
    height: 16px;
    width: 16px;
    border: 2px solid #222;
    border-radius: 100%;
    transform: rotate(45deg);
  }

  &:after {
    content: "";
    position: absolute;
    top: 3px;
    left: 10px;
    height: 12px;
    width: 2px;
    background-color: #222;
    transform: rotate(-49deg);
  }
}

.white-x {
  height: 40px;
  width: 40px;

  &:before,
  &:after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    height: 2px;
    width: 26px;
    background-color: white;
  }

  &:before {
    transform: translate(-50%, -50%) rotate(-45deg);
  }

  &:after {
    transform: translate(-50%, -50%) rotate(45deg);
  }
}

input[type="checkbox"] {
  display: none;
}

#menuCheckbox:checked ~ .side-menu {
  left: 0;
  box-shadow: 2px 0px 18px 0px rgba(0, 0, 0, 0.5);
}

#searchCheckbox:checked {
  & ~ .top-container .top-center-container {
    .top-text-container {
      left: -100%;
    }
    .top-search-container {
      left: 0;
    }
  }
}

.top-container {
  z-index: 10;
  height: 200px;
  width: 88%;
  max-width: 1200px;
  padding-top: 25px;
  margin: -100px auto 50px;
  background-color: white;
  text-align: center;
  box-shadow: 0px 3px 12px 0px rgba(0, 0, 0, 0.2);

  p {
    color: #777;
    margin: 0 auto 25px;
  }

  h2 {
    color: #2b2b2b;
    margin: 0 auto 12px;
  }
}

.top-center-container {
  height: 190px;
  width: 84%;
  overflow-x: hidden;
}

.inner-container {
  width: 100%;
  overflow: hidden;
  position: absolute;

  &.top-text-container {
    left: 0;
    transition: 0.4s ease;
  }

  &.top-search-container {
    left: 100%;
    transition: 0.4s ease;

    .search-text {
      position: absolute;
      top: 40px;
      left: 0;
      font-family: "Open Sans", sans-serif;
      font-size: 16px;
      color: #2b2b2b;
    }
  }
}

.top-search {
  display: block;
  position: relative;
  padding: 0 20px;
  font-family: "Open Sans", sans-serif;
  font-size: 44px;
  height: 70px;
  width: 85%;
  color: #2b2b2b;
  border: none;
  border-bottom: 2px solid #999;

  &:focus {
    outline: none;
    border-bottom: 2px solid #2b2b2b;
  }
}

.top-main-text {
  text-transform: uppercase;
  font-family: "Open Sans", sans-serif;
  font-size: 24px;
  line-height: 30px;
}

.image-layout-selector {
  display: flex;
  justify-content: center;
  margin: 20px 0;
}

.layout-option {
  width: 40px;
  height: 40px;
  border: 2px solid #ccc;
  margin: 0 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: background-color 0.3s ease;
  display: none !important;
}

.layout-option.active {
  background-color: #007bff;
  color: white;
}

.rotated {
  transform: rotate(45deg);
}

.bars {
  height: 60%;
  width: 60%;

  &:before,
  &:after {
    content: "";
    background-color: #999;
    transition: 0.3333s ease;
  }

  &:before {
    position: absolute;
    top: 60%;
    left: 0;
  }

  &:after {
    position: absolute;
    top: 0;
    left: 0;
  }

  &.rotated {
    transform: translate(-50%, -50%) rotate(90deg);
  }
}

.image-grid-container {
  margin-bottom: 40px;
  width: 88%;
  max-width: 1200px;
}

.image-column {
  position: relative;
  width: 33.3333333%;

  &.rows {
    width: 100%;
  }
}

.responsive-iframe {
  width: 100%;
  height: 100%;
  border: none;
}

.image {
  width: 88%;
  margin-bottom: 30px;
  box-shadow: 0px 8px 25px -5px rgba(0, 0, 0, 0.5);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  &.wide {
    padding-top: 50%;
  }

  &.square {
    padding-top: 80%;
  }

  .hidden {
    display: none !important;
  }

  &.long {
    padding-top: 110%;
  }

  &:before {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-family: "Montserrat", sans-serif;
    font-size: 18px;
    font-weight: 600;
    color: white;
  }
}
