/* variable */

.uimax-block-section-17 {
  padding-top: 80px;
  padding-bottom: 80px;
  background: "../../images/bg/section-bg-1.jpg" no-repeat fixed;
  background-position: center;
  background-size: cover;
  position: relative;
}
.uimax-block-section-17 .heading-title {
  text-align: center;
  color: #fff;
  text-transform: uppercase;
  font-size: 40px;
  font-weight: 700;
  margin-bottom: 60px;
}
.uimax-block-section-17 .slick-arrow {
  position: absolute;
  top: 50%;
  display: inline-block;
  z-index: 60;
  width: 30px;
  height: 30px;
  line-height: 30px;
  color: #444;
  text-align: center;
  margin-top: -10px;
  transform: translate(0, -50%);
  -webkit-transform: translate(0, -50%);
  -moz-transform: translate(0, -50%);
  border: 1px solid #aaaaaa;
  background: #fff;
  cursor: pointer;
  transition: all 0.3s ease;
  -webkit-transition: all 0.3s ease;
  -moz-transition: all 0.3s ease;
  opacity: 0;
}
.uimax-block-section-17 .slick-arrow:hover {
  opacity: 1;
  background: #2196f3;
  border-color: #2196f3;
  color: #fff;
}
.uimax-block-section-17 .slick-arrow.btn-prev {
  right: 100%;
  margin-right: 10px;
}
.uimax-block-section-17 .slick-arrow.btn-next {
  left: 100%;
  margin-left: 10px;
}
.uimax-block-section-17 .slick-dots {
  display: block;
  text-align: center;
  margin: 15px 0 0;
  position: absolute;
  width: 100%;
  bottom: -30px;
  left: 0;
}
.uimax-block-section-17 .slick-dots > li {
  display: inline-block;
  position: relative;
  margin: 0 5px;
}
.uimax-block-section-17 .slick-dots > li button {
  border: 1px solid #323a45;
  background: transparent;
  font-size: 0px;
  padding: 0;
  width: 12px;
  height: 12px;
  border-radius: 50%;
}
.uimax-block-section-17 .slick-dots > li button:focus {
  outline: none;
}
.uimax-block-section-17 .slick-dots > li button:hover {
  background: #2196f3;
  border-color: #2196f3;
}
.uimax-block-section-17 .slick-dots > li.slick-active button {
  background: #2196f3;
  border-color: #2196f3;
}
.uimax-block-section-17 .testimonial-slider {
  position: relative;
  background: rgba(255, 255, 255, 0.9);
  padding: 60px;
}
.uimax-block-section-17 .testimonial-slider:before {
  content: "";
  position: absolute;
  left: 20px;
  top: 20px;
  right: 20px;
  bottom: 20px;
  border: 1px solid #ccc;
}
.uimax-block-section-17 .testimonial-slider .item:focus {
  outline: none;
}
.uimax-block-section-17 .testimonial-slider .testimonial-item {
  position: relative;
  text-align: center;
}
.uimax-block-section-17 .testimonial-slider .testimonial-item .img-wrapper {
  display: inline-block;
  width: 80px;
  height: 80px;
  overflow: hidden;
  position: relative;
  z-index: 1;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  margin-bottom: 30px;
}
@media (max-width: 768px) {
    .uimax-block-section-17 .testimonial-slider .testimonial-item .block-quote {
      margin: 0 auto 15px; /* Assurez-vous que les marges sont bien centrées */
      padding: 10px; /* Ajoutez un peu d'espace autour */
    }
  
    .uimax-block-section-17 .testimonial-slider .testimonial-item .block-quote p {
      margin: 5px; /* Ajustez les marges pour plus d'espace */
      font-size: 14px; /* Réduisez légèrement la taille de la police si nécessaire */
      line-height: 1.5; /* Ajoutez de l'espacement entre les lignes */
    }
  
    .uimax-block-section-17 .testimonial-slider .testimonial-item .block-quote p:before,
    .uimax-block-section-17 .testimonial-slider .testimonial-item .block-quote p:after {
      margin-right: 5px; /* Ajoutez de l'espace autour des guillemets */
      margin-left: 5px;
    }
  }
  
.uimax-block-section-17 .testimonial-slider .testimonial-item .info .name {
  font-size: 16px;
  text-transform: uppercase;
  font-weight: 700;
  color: #2196f3;
  margin-top: 0px;
  margin-bottom: 0px;
}
.uimax-block-section-17 .testimonial-slider .testimonial-item .info .position {
  font-weight: 300;
  color: #323a45;
}
